h1 {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 55px;
    letter-spacing: 2px;
    // background: blue;
    font-family: 'Rubik Moonrocks', cursive;
}

.body {
    height: calc(100vh - 60px);
    overflow: hidden;
    main {
        display: flex;
        font-size: 15px;
        height: 45px;
        div{
            transition: .5s;
            height: 100%;
            display: flex;
            overflow-y: hidden;
            overflow-x: auto;
            &::-webkit-scrollbar {
                -webkit-appearance: none;
            }
            // &::-webkit-scrollbar-button:increment,&::-webkit-scrollbar-button {
            //     display: none;
            // } 
            // &:hover{
            //     transition: .5s;
            //     &::-webkit-scrollbar:horizontal {
            //         height: 8px;
            //     }

            // }
            &::-webkit-scrollbar:horizontal {
                height: 7px;
            }
            &:hover{
                &::-webkit-scrollbar-thumb {
                    background-color: #797979;
                    // border-radius: 20px;
                    // border: 2px solid #e6e6e6;
                }
            }
            &::-webkit-scrollbar-thumb {
                transition: .5s;
                background-color: #969696;
                border-radius: 20px;
                border: 2px solid #e6e6e6;
            }
            &::-webkit-scrollbar-track {
                border-radius: 10px;  
                background: #e6e6e600;
            }
            a{
                transition: .5s;
                margin: 3px 10px 0;
                height: 80%;
                display: flex;
                align-items: center;
                justify-content: center;
                min-width: 150px;
                // text-align: center;
                // padding: 5px 10px;
                background: rgb(105, 36, 255);
                color: white;
                border-radius: 15px;
                position: relative;
                &.act{
                    opacity: .6;
                    // color: black;
                }
                &.vip{
                    background: yellow;
                    color: black;
                    font-weight: 900;
                    letter-spacing: 1px;
                    &.act{
                        opacity: 1;
                        background: rgb(255, 232, 30);
                        // color: black;
                    }
                }
                svg{
                    position: absolute;
                    transform: rotate(0.09turn);
                    font-size: 26px;
                    font-weight: 900;
                    &.ico1 {
                        // inset: auto auto -5px -8px;
                        bottom: -5px;
                        left: -8px;
                    }
                    &.ico2 {
                        top: -8px;
                        right: -8px;
                        // inset: -10px -8px auto auto;
                    }
                }
            }
        }
        .icons{
            width: 40px;
            // background: rgb(2,0,36);
            // background: linear-gradient(90deg, rgba(2,0,36,0.30603037093774965) 61%, rgba(0,0,0,1) 85%);
            display: flex;
            align-items: center;
            justify-content: center;
            span{
                svg{
                    color: black;
                    font-size: 27px;
                }
            }
        }
    }
}