
.swiper {
   /* height: 100vh; */
   height: calc(100vh - 106px);
   user-select: none;
}
.mySwiper{
   background-position: center;
   background-size: cover;
}
.swiper-slide {
   text-align: center;
   font-size: 18px;
   background: #fff;

   /* Center slide text vertically */
   display: -webkit-box;
   display: -ms-flexbox;
   display: -webkit-flex;
   display: flex;
   -webkit-box-pack: center;
   -ms-flex-pack: center;
   -webkit-justify-content: center;
   justify-content: center;
   -webkit-box-align: center;
   -ms-flex-align: center;
   -webkit-align-items: center;
   align-items: center;
}

.swiper-slide>section {
   width: 100%;
   height: 100%;
   background: rgba(0, 0, 0, 0.407);
   backdrop-filter: blur(15px);
   position: relative;
}

.swiper-slide section img {
   display: block;
   width: 100%;
   height: 100%;
   object-fit: contain;
}

.swiper-slide section p {
   /* background: rgba(255, 255, 255, 0.432); */
   display: flex;
   justify-content: flex-end;
   align-items: flex-end;
   position: absolute;
   width: 100%;
   height: 100%;
   bottom: 0;
   z-index: 3;
}

.swiper-slide section p strong {
   display: block;
   width: 100%;
   padding: 30px 0;
   background: rgba(0, 0, 0, 0.458);
   color: white;
   font-family: 'Lobster', cursive;
   font-size: 18px;
   letter-spacing: 1.6px;
}

.swiper-pagination {
   color: white;
}

.paginacionSlider .swiper-pagination .swiper-pagination-bullet {
   width: 25px;
   height: 25px;
   text-align: center;
   line-height: 25px;
   font-size: 12px;
   font-weight: 600;
   color: #000;
   opacity: 1;
   background: rgba(255, 255, 255, 0.57);
}

.paginacionSlider .swiper-pagination .swiper-pagination-bullet-active {
   color: #fff;
   background: #007aff;
}

.sliderMonster {
   width: 100%;
   height: 90vh;
   max-height: 700px;
   padding-top: 50px;
   padding-bottom: 50px;
   backdrop-filter: blur(10px);
   /* filter: blur(10px); */
}

.sliderMonster .swiper-slide {
   background-position: center;
   background-size: cover;
   width: 550px;
   height: 100%;
   background: #0000 ;
   /* background: blue; */
}

.sliderMonster .swiper-slide section {
   background: #0000;
   backdrop-filter: blur(0);
   background-position: center;
   background-size: cover;
   /* background: blue; */
}

