.backg{
   background-image: url("background.jpg");
   background-position: center;
   background-size: cover;
}
.backg .swiper-slide{
   background: #0000;
   background-position: center;
   background-size: cover;
}
.sliderZoom section p{
   color: white;
}
.sliderZoom section p strong{
   background: #0000;
   font-size: 23px;
   letter-spacing: 4px;
   position: absolute;
   top: 0px;
}
.sliderZoom section p small{
   display: block;
   width: 100%;
   font-size: 17px;
   position: absolute;
   bottom: 40px;
   left: 0;
}

.sliderVuelta {
   display: block;
   max-width: 500px;
   height: 90vh;
   /* max-height: 600px; */
   /* background: red; */
}

.sliderVuelta .swiper-slide {
   background-position: center;
   background-size: cover;
   width: 95%;
   max-width: 630px;
   height: 95%;
}
.sliderEffectCards .swiper-slide,
.sliderEffectOnTopCard .swiper-slide {
   display: flex;
   align-items: center;
   justify-content: center;
   border-radius: 18px;
   font-size: 22px;
   font-weight: bold;
   color: #fff;
}

.sliderEffectCards {
   width: 95%;
}

.sliderEffectOnTopCard .swiper-slide {
   border-radius: 0;
}
